import Header from "@/components/common/Header";
import TabItemMallAdvertise from "@/components/layout/TabItemMallAdvertise";
import DividedArea from "@/components/common/DividedArea";
import FlexWrap from "@/components/layout/FlexWrap";
import Divider from "@/components/common/Divider";
import Scroll from "@/components/common/Scroller";
import GoodsList from "@/components/layout/GoodsList";
import { ShopGoodsListPush, ScenceGoodsClass } from "@/servers/api";

export default {
  name: "",
  props: [""],
  data() {
    return {
      TitleObjData: {
        titleContent: "景区消费",
        showLeftBack: true,
        showRightMore: false
      },
      title: "精选好物",
      dataList: [
        {
          name: "景区有礼",
          link: "/scenceconsumdetails?title=景区有礼&id=38&type=1&flag=3",
          class: "iconfont iconjingquhaoli1"
        },
        {
          name: "节日佳品",
          link: "/scenceconsumdetails?title=节日佳品&id=40&type=1&flag=3",
          class: "iconfont iconjierijiapin"
        },
        {
          name: "地方好物",
          link: "/scenceconsumdetails?title=地方好物&id=64&type=1&flag=3",
          class: "iconfont icondifanghaowu"
        },
        {
          name: "欣怡开市",
          link: "/scenceconsumdetails?title=欣怡开市&id=85&type=1&flag=3",
          class: "iconfont iconxinyikaishi"
        }
      ],
      goodsListData: []
    };
  },

  components: {
    Header,
    TabItemMallAdvertise,
    DividedArea,
    FlexWrap,
    Divider,
    Scroll,
    GoodsList
  },

  computed: {
    scenceGiftsContent() {
      return { height: document.documentElement.clientHeight - 45 + "px" };
    }
  },

  beforeMount() {},

  mounted() {
    this.setTitle();
    this.getClassList();
    this.getData();
    this.getBannerImgFn("4");
  },

  methods: {
    setTitle() {
      this.TitleObjData.titleContent = this.$route.query.title;
    },
    // 获取分类列表
    getClassList() {
      ScenceGoodsClass({})
        .then(res => {
          // console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 初始化数据
    getData() {
      ShopGoodsListPush({
        type: 1
      })
        .then(res => {
          if (res.result === 1) {
            this.goodsListData = res.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },

  watch: {}
};



